 all and (min-width: $pc2-media) and (max-width: $pc3s-media) {\r\n    @content;\r\n  }\r\n}\r\n\r\n// 電腦\r\n@mixin pc-width {\r\n  @media all and (min-width: $pc-media) and (max-width: $pc2s-media) {\r\n    @content;\r\n  }\r\n}\r\n\r\n// 平板\r\n@mixin pad-width {\r\n  @media all and (min-width: $pad-media) and (max-width: $pcs-media) {\r\n    @content;\r\n  }\r\n}\r\n\r\n// 小平板\r\n@mixin small-pad-width {\r\n  @media all and (min-width: $phone-media) and (max-width: $pads-media) {\r\n    @content;\r\n  }\r\n}\r\n// 手機\r\n@mixin phone-width {\r\n  @media all and (max-width: $phones-media) {\r\n    @content;\r\n  }\r\n}\r\n\r\n// Property\r\n$color_main: #2c3238;\r\n$color_second: #3a3f48;\r\n$color_third: #697180;\r\n$color_third_hover: #b7c2d6;\r\n$color_white: #fff;\r\n$color_orange: #ffb34a;\r\n$color_yellow: #ffd633;\r\n$color_apple_green: #78be20;\r\n$color_green: #2ebc4f;\r\n$color_black: #333;\r\n$color_blue_start: #19283d;\r\n$color_blue_end: #1d678f;\r\n$color_rain: #4dacff;\r\n$color_white_cloud: #f2f9fe;\r\n$color_grey_cloud: #ccc;\r\n\r\n$color_font_main: #868e9c;\r\n\r\n// Mixin\r\n@mixin size($width: 100%, $height: 100%) {\r\n  width: $width;\r\n  height: $height;\r\n}\r\n\r\n@mixin flex-layout($dir: row, $justify-content: center, $align-items: center) {\r\n  display: flex;\r\n  flex-direction: $dir;\r\n  justify-content: $justify-content;\r\n  align-items: $align-items;\r\n}\r\n\r\n@mixin border($width: 1px, $type: solid, $color: black) {\r\n  border: $width $type $color;\r\n}\r\n\r\n@mixin stroke($color: black, $width: 1px) {\r\n  stroke: $color;\r\n  stroke-width: $width;\r\n}\r\n\r\n@mixin independent($color: rgb(248, 130, 130), $zIndex: 1) {\r\n  position: absolute;\r\n  content: '';\r\n  background-color: $color;\r\n  z-index: $zIndex;\r\n}\r\n\r\n.tch-r {\r\n  font-family: 'Bosch Sans CHS Regular';\r\n}\r\n\r\n.tch-b {\r\n  font-family: 'Bosch Sans CHS Bold';\r\n}\r\n\r\n.eng-bl {\r\n  font-family: 'Bosch Sans Black';\r\n}\r\n\r\n.eng-bl-l {\r\n  font-family: 'Bosch Sans BlackItalic';\r\n}\r\n\r\n.eng-b {\r\n  font-family: 'Bosch Sans Bold';\r\n}\r\n\r\n.eng-b-l {\r\n  font-family: 'Bosch Sans BoldItalic';\r\n}\r\n\r\n.eng-l {\r\n  font-family: 'Bosch Sans Light';\r\n}\r\n\r\n.eng-l-l {\r\n  font-family: 'Bosch Sans LightItalic';\r\n}\r\n\r\n.eng-m {\r\n  font-family: 'Bosch Sans Medium';\r\n}\r\n\r\n.eng-m-l {\r\n  font-family: 'Bosch Sans MediumItalic';\r\n}\r\n\r\n.eng-r {\r\n  font-family: 'Bosch Sans Regular';\r\n}\r\n\r\n.eng-r-i {\r\n  font-family: 'Bosch Sans RegularItalic';\r\n}\r\n\r\n.title {\r\n  font-size: 64px;\r\n}\r\n\r\n.sub-title {\r\n  font-size: 24px;\r\n}\r\n\r\n.bottom-line {\r\n  position: relative;\r\n  display: inline-block;\r\n  line-height: 2.75;\r\n\r\n  &::after {\r\n    @include independent(transparent);\r\n    @include size(100%, 4px);\r\n    content: '';\r\n    background-color: #000000;\r\n    left: 0px;\r\n    bottom: 0px;\r\n    z-index: 10;\r\n  }\r\n}\r\n\r\n// Btn for more\r\n.btn-more {\r\n  @include flex-layout(row, center, center);\r\n  position: relative;\r\n  display: inline-flex;\r\n  cursor: pointer;\r\n\r\n  .bg-circle {\r\n    $circle-radius: 150px;\r\n    @include independent(transparent);\r\n    @include size($circle-radius, $circle-radius);\r\n\r\n    cursor: auto;\r\n    pointer-events: none;\r\n    left: calc((#{$circle-radius} / 2 * (-1)) + 10px);\r\n    z-index: 1;\r\n    transform: scale(0.15);\r\n    opacity: 0;\r\n\r\n    transition: all 0.25s cubic-bezier(0.25, 1, 0.5, 1);\r\n\r\n    svg {\r\n      @include size($circle-radius, $circle-radius);\r\n\r\n      circle {\r\n        fill: rgb(143, 143, 143);\r\n      }\r\n    }\r\n  }\r\n\r\n  > svg {\r\n    z-index: 2;\r\n    margin-right: 20px;\r\n  }\r\n\r\n  > p {\r\n    z-index: 2;\r\n    display: inline-block;\r\n    transition: all 0.15s linear;\r\n  }\r\n\r\n  &:hover {\r\n    .bg-circle {\r\n      opacity: 1;\r\n      transform: scale(1);\r\n    }\r\n  }\r\n}\r\n\r\n// Btn - color for background\r\n.btn-more-color-orange {\r\n  .bg-circle {\r\n    svg {\r\n      circle {\r\n        fill: $color_orange;\r\n      }\r\n    }\r\n  }\r\n}\r\n.btn-more-color-black {\r\n  .bg-circle {\r\n    svg {\r\n      circle {\r\n        fill: $color_main;\r\n      }\r\n    }\r\n  }\r\n}\r\n.btn-more-color-apple-green {\r\n  .bg-circle {\r\n    svg {\r\n      circle {\r\n        fill: $color_apple_green;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// Btn - direction for move\r\n.btn-more-hover-right-5 {\r\n  &:hover {\r\n    > p {\r\n      transform: translateX(5px);\r\n    }\r\n  }\r\n}\r\n.btn-more-hover-right-10 {\r\n  &:hover {\r\n    > p {\r\n      transform: translateX(10px);\r\n    }\r\n  }\r\n}\r\n.btn-more-hover-right-15 {\r\n  &:hover {\r\n    > p {\r\n      transform: translateX(15px);\r\n    }\r\n  }\r\n}\r\n.btn-more-hover-right-20 {\r\n  &:hover {\r\n    > p {\r\n      transform: translateX(20px);\r\n    }\r\n  }\r\n}\r\n\r\n// Btn - size fo radius\r\n$circle-radius-75: 75px;\r\n$circle-radius-85: 85px;\r\n$circle-radius-100: 100px;\r\n\r\n$circle-radius-offset-10: 10px;\r\n$circle-radius-offset-20: 20px;\r\n$circle-radius-offset-30: 30px;\r\n$circle-radius-offset-40: 40px;\r\n$circle-radius-offset-50: 50px;\r\n\r\n.btn-more-size-75 {\r\n  .bg-circle {\r\n    @include size($circle-radius-75, $circle-radius-75);\r\n    left: calc((#{$circle-radius-75} / 2 * (-1)) + #{$circle-radius-offset-10});\r\n\r\n    svg {\r\n      @include size($circle-radius-75, $circle-radius-75);\r\n    }\r\n  }\r\n}\r\n.btn-more-size-85 {\r\n  .bg-circle {\r\n    @include size($circle-radius-85, $circle-radius-85);\r\n    left: calc((#{$circle-radius-85} / 2 * (-1)) + #{$circle-radius-offset-10});\r\n\r\n    svg {\r\n      @include size($circle-radius-85, $circle-radius-85);\r\n    }\r\n  }\r\n}\r\n.btn-more-size-100 {\r\n  .bg-circle {\r\n    @include size($circle-radius-100, $circle-radius-100);\r\n    left: calc((#{$circle-radius-100} / 2 * (-1)) + #{$circle-radius-offset-10});\r\n\r\n    svg {\r\n      @include size($circle-radius-100, $circle-radius-100);\r\n    }\r\n  }\r\n}\r\n","// TCH\r\n@font-face {\r\n  font-family: 'Bosch Sans CHS Regular';\r\n  src: url('../files/fonts/BoschSansCHS-Regular.eot');\r\n  src: url('../files/fonts/BoschSansCHS-Regular.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSansCHS-Regular.woff') format('woff'),\r\n    url('../files/fonts/BoschSansCHS-Regular.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans CHS Bold';\r\n  src: url('../files/fonts/BoschSansCHS-Bold.eot');\r\n  src: url('../files/fonts/BoschSansCHS-Bold.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSansCHS-Bold.woff') format('woff'),\r\n    url('../files/fonts/BoschSansCHS-Bold.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n// ENG\r\n@font-face {\r\n  font-family: 'Bosch Sans Black';\r\n  src: url('../files/fonts/BoschSans-Black.eot');\r\n  src: url('../files/fonts/BoschSans-Black.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-Black.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-Black.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans BlackItalic';\r\n  src: url('../files/fonts/BoschSans-BlackItalic.eot');\r\n  src: url('../files/fonts/BoschSans-BlackItalic.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-BlackItalic.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-BlackItalic.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans Bold';\r\n  src: url('../files/fonts/BoschSans-Bold.eot');\r\n  src: url('../files/fonts/BoschSans-Bold.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-Bold.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-Bold.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans BoldItalic';\r\n  src: url('../files/fonts/BoschSans-BoldItalic.eot');\r\n  src: url('../files/fonts/BoschSans-BoldItalic.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-BoldItalic.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-BoldItalic.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans Light';\r\n  src: url('../files/fonts/BoschSans-Light.eot');\r\n  src: url('../files/fonts/BoschSans-Light.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-Light.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-Light.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans LightItalic';\r\n  src: url('../files/fonts/BoschSans-LightItalic.eot');\r\n  src: url('../files/fonts/BoschSans-LightItalic.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-LightItalic.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-LightItalic.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans Medium';\r\n  src: url('../files/fonts/BoschSans-Medium.eot');\r\n  src: url('../files/fonts/BoschSans-Medium.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-Medium.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-Medium.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans MediumItalic';\r\n  src: url('../files/fonts/BoschSans-MediumItalic.eot');\r\n  src: url('../files/fonts/BoschSans-MediumItalic.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-MediumItalic.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-MediumItalic.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans Regular';\r\n  src: url('../files/fonts/BoschSans-Regular.eot');\r\n  src: url('../files/fonts/BoschSans-Regular.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-Regular.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-Regular.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Bosch Sans RegularItalic';\r\n  src: url('../files/fonts/BoschSans-RegularItalic.eot');\r\n  src: url('../files/fonts/BoschSans-RegularItalic.eot?#iefix') format('embedded-opentype'),\r\n    url('../files/fonts/BoschSans-RegularItalic.woff') format('woff'),\r\n    url('../files/fonts/BoschSans-RegularItalic.ttf');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n","@import '../common/style/globalconfigure';\n@import '../common/style/_globalstyle.scss';\n@import '../common/style/_font.scss';\n\n* {\n  margin: 0px;\n  padding: 0px;\n  box-sizing: border-box;\n  //@include border();\n}\n\n.main-container {\n  background: rgb(214, 216, 217);\n  background: linear-gradient(\n    90deg,\n    rgba(214, 216, 217, 1) 3%,\n    rgba(253, 251, 251, 1) 41%,\n    rgba(219, 221, 222, 1) 75%,\n    rgba(214, 216, 217, 1) 100%\n  );\n}\n\n#loading-bar {\n  @include independent(#fff, 1100);\n  @include size(100%, 100vh);\n  @include flex-layout(row, center, center);\n  position: fixed;\n\n  $offset: 187;\n  $duration: 1.4s;\n\n  .spinner {\n    animation: rotator $duration linear infinite;\n  }\n\n  @keyframes rotator {\n    0% {\n      transform: rotate(0deg);\n    }\n    100% {\n      transform: rotate(270deg);\n    }\n  }\n\n  .path {\n    stroke-dasharray: $offset;\n    stroke-dashoffset: 0;\n    transform-origin: center;\n    animation: dash $duration ease-in-out infinite, colors ($duration * 4) ease-in-out infinite;\n  }\n\n  @keyframes colors {\n    0% {\n      stroke: #4285f4;\n    }\n    25% {\n      stroke: #de3e35;\n    }\n    50% {\n      stroke: #f7c223;\n    }\n    75% {\n      stroke: #1b9a59;\n    }\n    100% {\n      stroke: #4285f4;\n    }\n  }\n\n  @keyframes dash {\n    0% {\n      stroke-dashoffset: $offset;\n    }\n    50% {\n      stroke-dashoffset: $offset/4;\n      transform: rotate(135deg);\n    }\n    100% {\n      stroke-dashoffset: $offset;\n      transform: rotate(450deg);\n    }\n  }\n}\n\n#menu-window {\n  @include size(100%, 100vh);\n  @include independent(rgba($color: black, $alpha: 0.5), 1000);\n  position: fixed;\n  display: none;\n\n  #menu-window-close {\n    float: right;\n    margin-top: 20px;\n    margin-right: 20px;\n    padding: 20px;\n  }\n}\n\nnav {\n  @include size(100%, auto);\n  @include flex-layout(row, space-between, center);\n  position: relative;\n\n  #home {\n    @include independent(transparent);\n    top: 35px;\n    left: 300px;\n  }\n\n  #menu {\n    $menu-height: 20px;\n    $ham-width: 2px;\n    @include size(35px, $menu-height);\n    @include independent(transparent, 1001);\n    @include flex-layout(row, space-around, center);\n    cursor: pointer;\n    top: 35px;\n    right: 300px;\n\n    span {\n      &::before {\n        @include independent(#000);\n        @include size(100%, $ham-width);\n        left: 0;\n        top: 0;\n        transition: width 0.35s 0.45s;\n      }\n\n      &::after {\n        @include independent(#000);\n        @include size(100%, $ham-width);\n        right: 0;\n        bottom: 0;\n        transition: width 0.35s 0.45s;\n      }\n    }\n\n    &::before {\n      @include independent(#000);\n      @include size(100%, $ham-width);\n      left: 0;\n      top: calc((#{$menu-height} / 2) - ($ham-width / 2));\n      transform-origin: center center;\n      transition: 0.45s cubic-bezier(0.33, 1, 0.68, 1);\n    }\n\n    &::after {\n      @include independent(#000);\n      @include size(100%, $ham-width);\n      right: 0;\n      bottom: calc((#{$menu-height} / 2) - ($ham-width / 2));\n      transform-origin: center center;\n      transition: 0.45s cubic-bezier(0.33, 1, 0.68, 1);\n    }\n  }\n\n  #menu.show {\n    span {\n      &::before {\n        right: 0;\n        width: 0;\n        transition: width 0.8s;\n      }\n\n      &::after {\n        width: 0;\n        transition: width 0.8s;\n      }\n    }\n\n    &::before {\n      transform: rotate(-45deg);\n      transition: transform 0.5s 0.65s cubic-bezier(0.33, 1, 0.68, 1);\n    }\n\n    &::after {\n      transform: rotate(45deg);\n      transition: transform 0.5s 0.65s cubic-bezier(0.33, 1, 0.68, 1);\n    }\n  }\n}\n\n.slogan {\n  @include size(100%, 330px);\n  @include flex-layout(row, center, center);\n  margin-top: 155px;\n  background: rgb(226, 0, 21);\n  background: linear-gradient(90deg, rgba(226, 0, 21, 1) 0%, rgba(185, 2, 118, 1) 100%);\n\n  .slogan-wrap {\n    @include size(500px, auto);\n    @include flex-layout(column, center, center);\n    color: #fff;\n\n    > :first-child {\n      font-size: 20px;\n      line-height: 1;\n      margin-bottom: 10px;\n    }\n\n    > :last-child {\n      font-size: 60px;\n      line-height: 1;\n    }\n  }\n}\n\n.description {\n  @include size(100%, 500px);\n  @include flex-layout(column, center, center);\n  padding: 20px 50px;\n\n  .content-wrap {\n    @include flex-layout(column, center, center);\n    @include size(800px, 300px);\n\n    font-size: 20px;\n  }\n}\n\n.proxy {\n  @include size(100%, 500px);\n}\n\n.footer {\n  background-color: #ffffff;\n\n  .top {\n    @include size(100%, auto);\n    @include flex-layout(row, center, flex-start);\n    padding: 100px 0px 15px 0px;\n\n    @include phone-width() {\n      flex-wrap: wrap;\n      height: auto;\n    }\n  }\n\n  .meddle {\n  }\n\n  .bottom {\n    @include size(100%, 22px);\n    svg {\n      @include size(100%, auto);\n    }\n  }\n\n  .footer-content {\n    @include size(auto, auto);\n    display: inline-block;\n    margin-right: 100px;\n    padding: 10px;\n\n    .title {\n      font-size: 18px;\n    }\n\n    .main-link {\n      font-size: 16px;\n    }\n\n    .sub-title {\n    